//从上到下的过度 name:slide-fade
.slide-fade-enter-active {
    transition: all 0.3s ease;
}

.slide-fade-leave-active {
    transition: all 0.3s ease;
}

.slide-fade-enter,
.slide-fade-leave-to {
    transform: translateY(-300px);
    opacity: 0;
}

//从上到下的过度 name:slide-up-fade
.slide-up-fade-enter-active {
    animation: upenter 0.7s ease-in-out;
}

.slide-up-fade-leave-active {
    animation: down 0.2s ease-in-out;
    opacity: 0;
}

.big-enter-active {
    animation: fangda 0.3s ease-in-out;
}

.big-leave-active {
    animation: suoxiao 0.3s ease-in-out;
}

@keyframes fangda {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes suoxiao {
    0% {
        opacity: 1;
        transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: scale(0.5);
    }
}

@keyframes down {
    0% {
        transform: translateY(0px);
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: translateY(200px) scaleY(0.8);
    }
}

@keyframes upenter {
    0% {
        transform: translateY(300px);
        opacity: 0;
    }
    30% {
        transform: translateY(-20px);
        opacity: 1;
    }
    60% {
        transform: translateY(5px);
    }
    100% {
        transform: translateY(0px);
    }
}

//从上到下的过度 name:slide-down-fade
.slide-down-fade-enter-active {
    animation: downenter 0.3s ease-in-out;
}

.slide-down-fade-leave-active {
    animation: bouncehide 0.3s ease-in-out;
}

@keyframes downenter {
    0% {
        transform: translateY(-200px);
        opacity: 0;
    }
    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}

@keyframes bouncehide {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

//从上到下的过度 name:slide
.slide-enter-active {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.slide-leave-active {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.slide-enter,
.slide-leave-to {
    transform: translateY(-10px);
    opacity: 0;
}

//弹性动画 name: bounce
.bounce-enter-active {
    animation: bounce-in .2s;
}

.bounce-leave-active {
    animation: bounce-in .2s reverse;
}

@keyframes bounce-in {
    0% {
        transform: scale(0.95);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(1);
    }
}

//放大退出动画 name:scale-leave
.scale-enter-active {
    opacity: 0;
}

.scale-leave-leave-active {
    transition: all .1s ease;
    opacity: 0;
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

.ripple {
    position: absolute;
    background: rgba(0, 0, 0, .15);
    border-radius: 100%;
    transform: scale(0);
    pointer-events: none;
    will-change: transform, opacity,animation;
}

.ripple.show {
    animation: ripple 0.8s ease-out;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.1s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes ripple {
    to {
        transform: scale(2);
        opacity: 0;
    }
}