.left-nav {
    height: 100%;
    .left {
        width: 200px;
        height: 100%;
        background-color: #ffffff;
        float: left;
        z-index: 2;
    }
    .left-name {
        font-size: 16px;
        height: 50px;
        padding-left: 30px;
        background-color: #ffffff;
        line-height: 50px;
        cursor: pointer;
    }
    .left-name:hover {
        color: #eb6504;
    }
    .active {
        background-color: #f9f9f9;
        color: #eb6504;
    }
    .right {
        position: relative;
        display: inline-block;
        width: calc(100% - 200px);
        height: 100%;
        z-index: 1;
    }
}