@load-bac: #080;
@common-blue: #4786ff;
.mtyas-noselect {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
    user-select: none;
}

.v-transfer-dom {
    position: fixed;
    z-index: 999999;
}

.dragscroll {
    cursor: -webkit-grab;
    &:active {
        cursor: -webkit-grabbing;
    }
}

.error_font {
    color: red!important;
    cursor: pointer;
}

.mtyas-single-select-list {
    width: 100%;
    max-height: 255px;
    overflow-y: auto;
    border-radius: 2px;
    background-color: #ffffff;
    box-shadow: 0 7px 17px 0 rgba(71, 71, 71, 0.14);
    animation-duration: 150ms;
    margin-top: 4px;
    margin-left: 0px;
    position: absolute;
    z-index: 1000;
    max-height: 280px;
    .totallist {
        .listtitle {
            height: 22px;
            line-height: 22px;
            padding-left: 10px;
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
        }
        ;
    }
    .searchContain {
        display: inline-block;
        width: 100%;
        position: relative;
        i {
            position: absolute;
            top: 10px;
            left: 15px;
            font-size: 14px;
            color: #C1C5CC;
        }
    }
    .search {
        font-size: 14px;
        margin: 0 auto;
        height: 34px;
        border-radius: 2px;
        border: none;
        background-color: #f4f5f7;
        margin-bottom: 6px;
        display: block;
        outline: none;
        padding-left: 14px;
        width: 100%;
        box-sizing: border-box;
        outline: none;
    }
    input:-ms-input-placeholder {
        color: #c2c5cd;
        opacity: 1;
        padding-left: 20px !important;
    }
    input::-webkit-input-placeholder {
        color: #c2c5cd;
        opacity: 1;
        padding-left: 20px !important;
    }
    .iconenter {
        transition: all 0.3s ease;
        transform: rotate(180deg);
    }
    .iconleave {
        transition: all 0.3s ease;
        transform: rotate(0deg);
    }
    .selectlist:hover {
        background-color: #f4f5f7;
    }
    .selectlist>div {
        width: 100%;
    }
    .radio-original {
        opacity: 0;
        outline: none;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0;
    }
    .radio-input {
        white-space: nowrap;
        cursor: pointer;
        outline: none;
        display: inline-block;
        line-height: 0.6;
        position: relative;
        vertical-align: middle;
    }
    .radio-inner {
        border: none;
        width: 0.5rem;
        position: relative;
        cursor: pointer;
        display: inline-block;
        box-sizing: border-box;
    }
    .hover {
        i {
            color: #4786ff;
            font-size: 14px;
        }
    }
    p.p_url {
        margin-left: 10%;
        font-size: 12px;
        color: #152230;
        margin-bottom: 2px;
        margin-top: 0px;
        width: 90%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    ul {
        .selectlist {
            height: 35px;
            line-height: 35px;
            padding-left: 15px;
            cursor: pointer;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
    .hover {
        .selectlist {
            color: #fff;
        }
        .p_url {
            color: #fff;
        }
    }
    p {
        margin-left: 10%;
        font-size: 14px;
        color: #152230;
        margin-bottom: 0px;
        margin-top: 2px;
        width: 100%;
        line-height: 20px;
    }
}

