.tip_info {
    display: inline-block;
    width: 18px;
    position: absolute;
    cursor: pointer;
    right: 0px;
    top: 5px;
    /* display: none; */
    i {
        font-size: 20px;
        color: #4786ff;
    }
    .arrow-up {
        width: 0;
        height: 0;
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-bottom: 3px solid red;
        margin-left: 7px;
        margin-top: -5px;
    }
    .tip_content {
        position: absolute;
        max-width: 400px;
        min-width: 200px;
        line-height: 18px;
        /* left: -25%; */
        z-index: 99999;
        p {
            background: #dc1e1e;
            border-radius: 2px;
            padding: 5px 10px;
            width: 200px;
        }
    }
}

.v-tip-container.v-tip-container-desc {
    position: absolute;
    top: -5px;
    left: 5px;
    z-index: 9999;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 200px;
    padding: 2px 8px;
    margin: 0;
    color: #000;
    font-size: 12px;
    background: #fff;
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, .1);
    box-shadow: 0 1px 6px rgba(0, 0, 0, .1);
    border: 1px solid #fff;
    border-radius: 4px;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
    will-change: opacity, transform;
    max-width: 500px;
}

.input {
    height: 35px;
    border-radius: 2px;
    background-color: #eff3f6;
    outline: none;
    border: solid 0.5px #EFF3F6;
    width: 150px;
    padding-left:5px; 
}

.input:focus {
    -webkit-transition: border linear .2s, -webkit-box-shadow linear .5s;
    border-color: #4786ff;
}

.sw-datepicker {
    position: relative;
    .date-head-title {
        font-size: 16px;
        padding: 0 5px;
        font-weight: bold;
    }
    .sw-datepicker-head {
        width: 100%;
        height: 35px;
        background-color: #fff;
        cursor: pointer;
        position: relative;
        user-select: none;
        border: solid 1px #D3D4D6;
        border-radius: 2px;
        transition: .3s;

    }
    .sw-datepicker-head:hover {
        border: solid 1px #4786ff;
    }
    .sw-d-placeholder {
        font-size: 16px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 10px;
    }
    .sw-d-icon {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        .sw-d-titicon {
            font-size: 23px;
            color: #989ca4;
        }
    }
    .sw-d-list {
        width: 322px;
        height: 358px;
        background-color: #fff;
        position: absolute;
        box-shadow: 0 7px 17px 0 rgba(71, 71, 71, 0.14);
        margin-top: 2px;
        border-radius: 5px;
        z-index: 1;
    }
    .sw-d-year {
        display: flex;
        padding: 20px;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        position: relative;
    }
    .sw-d-order {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .sw-d-date {
        display: flex;
        padding: 10px 10px 10px 10px;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .date-title {
        width: 42px;
        height: 42px;
        text-align: center;
        line-height: 42px;
        transition: all 0.3s ease;
        cursor: pointer;
        font-size: 16px;
    }
    .date-title-month {
        width: 100px;
        height: 70px;
        line-height: 70px;
        transition: all 0.3s ease;
        cursor: pointer;
    }
    .date-title-month:hover,
    .date-title:hover {
        /*background-color: #2d8cf0;*/
        background-color: #e1f0fe;
        border-radius: 5px;
    }
    .selected {
        background-color: #2d8cf0 !important;
        color: #fff !important;
        border-radius: 5px;
    }
    .year-icon-left {
        position: absolute;
        left: 30px;
        cursor: pointer;
        font-size: 20px;
    }
    .year-icon-right {
