



































































































































































































































































































































































































































































.maxW350 {
  max-width: 350px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 4px;
}

.flexotable {
  position: relative;
  overflow: auto;
  .clickpop {
    cursor: pointer;
  }
  .disnone {
    display: none;
  }
  tr:hover {
    i {
      cursor: default;
    }
  }
  .animated {
    animation-duration: 0.3s;
    animation-fill-mode: both;
  }
  .move {
    transition: transform 1s;
  }
  .showhand {
    cursor: pointer;
    span:hover {
      color: #da091e !important;
    }
  }
  .showhand:hover {
    color: #da091e !important;
  }
  width: 100%;
  .popover {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    background-color: #fff;
    padding: 14px;
    opacity: 1;
    min-width: 100px;
    max-width: 300px;
    border-radius: 5px;
    margin-bottom: 10px;
    word-wrap: break-word;
    .popover-arrow {
      position: absolute;
      bottom: 0px;
      left: 20%;
      i {
        display: inline-block;
        border: 6px solid transparent;
        border-top: 6px solid #fff;
        position: absolute;
      }
    }
  }
  .tip {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  .tractive {
  }
  .tablearea {
    .tablebord {
      padding: 0 20px;
      overflow-y: auto;
      overflow-x: auto;
    }
    .tablefix {
      overflow-x: hidden !important;
    }
    .public_table {
      width: 100%;
      table-layout: fixed;
      border: 0;
      border-collapse: collapse;
      .operatebutton {
        text-align: center;
      }
      .tbtitle {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      thead {
        tr {
          background-color: #ffffff;
        }
        th {
          font-size: 16px;
          font-weight: bold;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.25;
          letter-spacing: normal;
          color: #3a3a3a;
        }
        // th:first-child {
        // 	padding-left: 15px;
        // }
      }
      tbody {
        tr {
          /*background-color: #fafafa;*/ //
          td {
            border-bottom: 1px solid #e9ecef;
            /*padding-left: 0px;  //*/
            height: 49px;
            a {
              cursor: pointer;
              &:hover {
                color: #da091e;
              }
            }
          }
          td:first-child a {
            cursor: pointer;
          }
        }
      }
      th {
        position: relative;
        span {
          cursor: text;
          -moz-user-select: none;
          -khtml-user-select: none;
          user-select: none;
        }
        span:last-child {
          display: inline-block;
          position: relative;
          left: 2px;
          top: -2px;
        }
        i:first-child {
          display: inline-block;
          border: 6px solid transparent;
          border-bottom: 6px solid #87919f;
          top: 0;
          transform: translateY(-50%);
        }
        i:last-child {
          display: inline-block;
          border: 6px solid transparent;
          border-top: 6px solid #87919f;
          position: absolute;
          left: 0;
          bottom: 0;
          transform: translateY(50%);
        }
        .actionS {
          border-bottom: 6px solid #2a2a2a !important;
        }
        .actionX {
          border-top: 6px solid #2a2a2a !important;
        }
      }
      th,
      td {
        text-align: center; //
        padding-left: 10px;
        height: 50px;
      }
      td {
        span,
        a {
          font-size: 14px;
          color: #3a3a3a;
        }
        .iconColor {
          .tdicons {
            .iconfont {
              font-size: 20px;
              margin: 0 4px;
            }
          }
          .iconfont {
            color: #c2c5cd;
          }
        }
      }
      td:last-child {
        .td_operate {
          span {
            border-left: 1px solid #ebeff2;
            font-size: 14px;
            color: #ccc;
            display: inline-block;
            padding: 0 10px;
            cursor: pointer;
            margin-right: -3px;
          }
          span:first-child {
            margin-left: 0px;
            padding-left: 10px;
            border: 0;
          }
        }
      }
    }
  }
}
