


















































































































































































































































































.mtyas-m-select {
    .mtyas-noselect {
	    -moz-user-select: none;
	    -webkit-user-select: none;
	    -ms-user-select: none;
	    -khtml-user-select: none;
	    user-select: none;
	}
    ::-webkit-scrollbar {
        width: 5px;
        height: 8px
    }
    ::-webkit-scrollbar-thumb {
        background-color: #e9ecef;
        border-radius: 6px;
    }
    ::-webkit-scrollbar-track {
        background-color: #fff;
        border-radius: 6px;
    }
    min-width:150px;
    width: 100%;
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    .el-select-dropdown {
        margin-top: 3px !important;
    }
    .title {
        width: 100%;
        height: 38px;
        position: relative;
        cursor: pointer;
        border-radius: 2px;
        background-color: #f4f5f7;
        border: 0.5px solid #f4f5f7;
        line-height: 38px;
    }
    .disabled {
        color: #c1cdd6!important;
        cursor: not-allowed;
        i.iconfont {
            color: #cfcfcf;
        }
    }
    .placeholder {
        color: #c1cdd6!important;
    }
    .icon {
        position: absolute;
        top: 0%;
        right: 6%;
        color: #c2c5cd;
        line-height: 40px;
        font-size: 14px;
        i {
            font-weight: 400;
            font-size: 14px;
            color: #000;
        }
    }
    .searchword {
        width: 70%;
        position: absolute;
        top: 4%;
        left: 10px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 15px;
        line-height: 38px;
        color: #2a2a2a;
    }
    .hover {
        .icon-dangqiangouxuan {
            color: #4786ff;
            font-size: 12px;
        }
    }
}

.hasError {
    .mtyas-m-select {
        .title {
            border: solid 0.5px #f76280;
        }
    }
}
