






















































.search-box {
	position: relative;
	/*position: absolute;
	right: 0;*/
	display: flex;
	justify-content: center;
	align-items: center;
	.selectBox {}
	.search-icon {
		display: inline-block;
		width: 70px;
		height: 35px;
		background-color: #0380d3;
		color: #fff;
		border: 1px #0380d3 solid;
		cursor: pointer;
		transition: .3s;
		i {
			font-size: 16px;
			font-weight: bold;
			transition: .3s;
		}
		&:hover {
			background-color: #0374c0;
			i {
				transform: scale(1.5, 1.5);
			}
		}
	}
	.form-control {
		border: 1px #D3D4D6 solid !important;
		border-radius: 0px !important;
	}
	.mtyas-single-select {
		width: 120px !important;
		min-width: 120px !important;
		.title {
			border-top-right-radius: 0px;
			border-bottom-right-radius: 0px;
			width: 100% !important;
		}
	}
}
