

























.mtyas-spin {
	position: absolute;
	background-color: rgba(0, 0, 0, 0);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 99999;
	.loading-box {
		position: absolute;
		display: flex;
		align-items: center;
		width: auto;
		height: 50px;
		line-height: 50px;
		text-align: center;
		background-color: #FFFFFF;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		border-radius: 4px;
		box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
		padding-right: 20px;
		letter-spacing: 2px;
	}
	.msg{
		font-weight:bold ;
	}
	.spinner {
		border-radius: 50%;
		display: inline-block;
		position: relative;
		height: 60px;
		width: 60px;
		background-color: #e0e0eb;
		transform: scale(.5);
	}
	.spinnerTwo:before {
		position: absolute;
		top: 30px;
		left: 27.5px;
		content: "";
		height: 35px;
		width: 5px;
		background-color: #535657;
		transform-origin: 50% 0%;
		border-radius: 8px;
		animation: clockmin 2s linear infinite;
	}
	.spinnerTwo:after {
		position: absolute;
		content: "";
		height: 35px;
		width: 2px;
		top: 30px;
		left: 29px;
		transform-origin: 50% 0%;
		background-color: #D1462F;
		border-radius: 2px;
		animation: clocksec 0.5s linear infinite;
	}
	.spinnerTwo span {
		position: absolute;
		top: 10px;
		left: 27.5px;
		height: 20px;
		width: 5px;
		border-radius: 2px;
		background: #535657;
		transform-origin: 50% 100%;
		animation: clockhour 10s linear infinite;
	}
	@keyframes clockhour {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	@keyframes clockmin {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	@keyframes clocksec {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
}
