



























































































































































.pagination {
	font-size: 14px;
	display: flex;
	align-items: center;
	.location {
		display: inline-block;
		font-size: 14px;
		color: #35415E;
		margin-right: 24px;
	}
	.paging {
		display: inline-block;
		position: relative;
		li {
			&.prev,
			&.next {
				min-width: 50px;
				// transform: rotate(90deg);
				// &:after {
				//     content: "V";
				// }
			}
			// &.next {
			//     transform: rotate(-90deg);
			//     &:after {
			//         content: "V";
			//     }
			// }
			&.disabled {
				opacity: 0.6;
				cursor: not-allowed;
				&:hover {
					color: #304150;
					background-color: white;
					border: 1px solid rgba(190, 190, 190, 1);
				}
			}
			font-size:14px;
			color:#304150;
			margin-right:12px;
			cursor: pointer;
			padding: 5px 10px;
			box-sizing:border-box;
			text-align: center;
			float: left;
			border: 1px solid rgba(190, 190, 190, 1);
			border-radius: 3px;
			transition:.3s; 
			&:hover,
			&.active {
				color: white;
				background: #0380d3;
				border-color: #0380d3;
				box-shadow: 0 0 0 2px rgba(45, 140, 240, .5) !important;
			}
			&.first,
			&.last {
				
			}
		}
	}
	.drop_down_menu {
		border-radius: 3px;
		border: 1px solid rgba(190, 190, 190, 1);
		position: relative;
		font-size: 12px;
		padding-left: 5px;
		/deep/ .select_item {
			/*width: 70px;*/
		}
		/deep/ .menu .common_li {
			font-size: 12px;
		}
		/deep/.triangle {
			top: -1px;
		}
	}
	.go_to {
		margin-left: 22px;
		display: inline-block;
		.one {
			margin-right: 12px;
		}
		.two {
			margin-left: 12px;
		}
		input {
			width: 40px;
			height: 25px;
			line-height: 25px;
			padding-left: 5px;
		}
	}
}
