




















































































































































































































































































.drop_down_menu {
    width: 100px;
    height: 32px;
    line-height: 32px;
    display: inline-block;
    border: 1px solid rgba(212, 223, 229, 1);
    box-sizing: border-box;
    background: white;
    border-radius: 3px;
    padding-left: 15px;
    cursor: pointer;
    padding-right: 26px;
    position: relative;

    .select_item {
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        display: inline-block;
        white-space: nowrap;
    }

    .triangle {
        position: absolute;
        top: 0px;
        right: 10px;
        font-size: 10px;
        transition: transform .1s;
        -moz-transition: transform .1s;
        -webkit-transition: transform .1s;
        -o-transition: transform .1s;
    }

    box-sizing:border-box;

    &:hover {
        border: 1px solid #55A8FD;
    }

    &.opened {
        .triangle {
            transform: rotate(180deg);
        }

        border:1px solid #55A8FD;

        .menu {
            height: initial;
            z-index: 100
        }
    }

    .menu {
        background: white;
        position: absolute;
        left: 0px;
        top: 32px;
        width: 100%;
        max-height: 170px;
        height: 0px;
        overflow: auto;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 7px 17px 0px rgba(107, 126, 151, 0.3);
        border-radius: 2px;
        &.isScroll {
            overflow: scroll;
        }

        &.hasCheckBox.crosswise {
            width: 250px;
        }

        .filter_bar {
            width: 100%;
            height: initial;
            line-height: initial;
            background: white;
            padding: 10px 10px;
            margin: initial;
            box-sizing: border-box;

            input {
                width: 100%
            }
        }

        .common_li {
            padding-left: 10px;
            font-size: 14px;
            height: 34px;
            line-height: 34px;
            cursor: pointer;

            &.hasCheckBox.crosswise {
                float: left;
                width: 71px;
            }

            &.hidden {
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }

            &:hover:not(.hasCheckBox) {
                background: #F1F5FC;
            }

            .selected_icon {
                width: 20px;
                height: 34px;
                line-height: 34px;
                display: inline-block;
                opacity: 0;
                filter: alpha(opacity=0);
                float: left;
                text-align: center;
            }

            &.selected {
                .selected_icon {
                    opacity: 1;
                    filter: alpha(opacity=100);
                    font-size: 12px;
                    color: #55A8FD;
                }
            }

            /deep/ .check_box {
                position: relative;
                top: 2px;
                margin-right: 4px;

                &.checked {
                    span:before {
                        top: -10px;
                    }
                }
            }
        }

        &.fixed {
            position: fixed;
        }
    }
}
