





























































































.mtyas-checkbox {
    display: inline-block;
    .mtyas-noselect {
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        -khtml-user-select: none;
        user-select: none;
    }
    cursor: pointer;
    input {
        display: none;
    }
    input + label {
        -webkit-appearance: none;
        border: 1px solid #aaa;
        padding: 8px;
        display: inline-block;
        position: relative;
        margin-right: 5px;
        cursor: pointer;
        border-radius: 2px;
        overflow: hidden;
    }
    .checked-label {
        border: 1px solid #0380d3;
        line-height: 16px;
        background: #0380d3;
        box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
    }
    .checked {
        background: #0380d3;
        transform: translate(-50%, -50%) scale(1) !important;
        border-radius: 2px;
    }
    .mtyas-checkbox-point {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0.3);
        transition: all 0.2s ease;
        font-size: 0.6rem;
        color: #fff;
    }
}

.mtyas-checkbox.disabled {
    cursor: not-allowed;
    .check-label {
        cursor: not-allowed;
        border: 1px solid #cfcfcf;
        line-height: 16px;
        background: #cfcfcf;
        i.iconfont {
            background: #cfcfcf;
            transform: translate(-50%, -50%) scale(1) !important;
            border-radius: 2px;
        }
    }
    .desc {
        color: #cfcfcf;
    }
}
