






























































































































































































    .home {
		height: 100%;
		overflow-x: hidden;
	}
    .header{
		height: 10%;
		width: 100%;
    }
	.pc-banner {
        background:rgba(255,255,255,0);
        font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
        font-size: 14px;
        color:#000;
        margin: 50px 0px 0px 100px;
        padding: 0;
    }
    .swiper-container {
        width: 100%;
        height: auto;
        margin-left: auto;
        margin-right: auto;
	}
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        // background: #fff;
		// height: 215px;
        
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
	}
	
	.swiper-button-prev{
		width: 30px;
		height: 70px;
		margin-left: 20px;
		margin-top:1px;
		background:rgba(255,255,255,0.3);
	}
	.swiper-button-next{
		width: 30px;
		height: 70px;
		margin-right: 20px;
		 margin-top:1px;
		background:rgba(255,255,255,0.3);
	}
	.logoDiv{
		height: 30px;
		width: 200px;
		margin-top:20px; 
	}
