.view_head {
    min-width: 1366px;
    position: relative;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    display: block;
    z-index: 101;
    height: 60px;
    // background-color: #da091e;
    overflow: hidden;
    background: linear-gradient(to right, #409499, #26674c);
    .view_head_logo {
        width: 20%;
        height: 60px;
        .header_left {
            cursor: pointer;
            color: #8c979b;
            margin-right: 30px;
            height: 60px;
            line-height: 60px;
            font-size: 16px;
            float: left;
            a {
                display: inline-block;
                width: 100%;
                text-decoration: none;
                color: #8c979b;
                font-size: 16px;
                line-height: 60px;
                .logo-box {
                    width: 100%;
                    height: 60px;
                    float: left;
                }
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
        .header_left:first-child {
            margin-right: 0.1rem;
            margin-left: 39px;
            vertical-align: super;
            min-width: 1.9rem;
            font {
                font-size: 26px;
                color: #fff;
                position: relative;
                top: 1px;
                b {
                    padding: 0 8px;
                    top: -2px;
                    position: relative;
                }
            }
        }
        .header_right {
            float: right;
            font-size: 14px;
            cursor: pointer;
            height: 60px;
            line-height: 60px;
            margin-right: 39px;
            color: #fff;
            width: 50%;
            text-align: right;
            i {
                font-size: 12px;
            }
            .currDept {
                margin-right: 8px;
            }
            .userName {
                font-size: 16px;
                color: #8c979b;
                margin-left: 25px;
            }
            .icon-tuichu {
                font-size: 16px;
                color: #8c979b;
                margin-left: 20px;
            }
            .dropDown {
                display: inline-block;
                .icon {
                    display: inline-block;
                }
            }
        }
    }
}

.iconenter {
    transition: all .3s ease;
    transform: rotate(180deg);
}

.iconleave {
    transition: all .3s ease;
    transform: rotate(0);
}