


















































































































































































































































































.mtyas-multi-select {
	.mtyas-noselect {
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		-khtml-user-select: none;
		user-select: none;
	}
	 ::-webkit-scrollbar {
		width: 5px;
		height: 8px
	}
	 ::-webkit-scrollbar-thumb {
		background-color: #e9ecef;
		border-radius: 6px;
	}
	 ::-webkit-scrollbar-track {
		background-color: #fff;
		border-radius: 6px;
	}
	width: 100%;
	display: inline-block;
	position: relative;
	text-align: left;
	.el-select-dropdown {
		margin-top: 3px !important;
	}
	.title {
		width: 100%;
		position: relative;
		cursor: pointer;
		height: 35px;
		background-color: #ffffff;
		text-align: center;
		border: 1px #D3D4D6 solid;
		border-radius: 4px;
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		padding: 0 3%;
		.icon {
			font-size: 14px;
			color: #989ca4;
			line-height: 30px;
			margin-left: 10px;
			margin-right: 6px;
			i {
				font-weight: 400;
				font-size: 16px;
			}
		}
	}
	.disabled {
		color: #c1cdd6!important;
		cursor: not-allowed;
	}
	.hover {
		.icon-dangqiangouxuan {
			color: #4786ff;
			font-size: 12px;
		}
	}
	.searchword {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-size: 15px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #989ca4 !important;
	}
}

.hasError {
	.mtyas-multi-select {
		.title {
			border: solid 0.5px #f76280;
		}
	}
}
