





























.mtyas-toast {
	z-index: 999999;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

.mtyas-toast-body {
	z-index: 999999;
	position: absolute;
	top: 20%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	min-width: 250px;
	max-width: 900px;
	min-height: 20px;
	background-color: rgba(49, 49, 49, 0.9);
	box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
	padding: 15px 10px;
	text-align: center;
	border-radius: 4px;
	.mtyas-toast-msg {
		position: relative;
		color: #fff;
		width: 100%;
		height: 100%;
		font-size: 16px;
		font-weight: 400;
	}
}
