


















































































































































































.mtyas-single-select {
	 ::-webkit-scrollbar {
		width: 5px;
		height: 8px
	}
	 ::-webkit-scrollbar-thumb {
		background-color: #e9ecef;
		border-radius: 6px;
	}
	 ::-webkit-scrollbar-track {
		background-color: #fff;
		border-radius: 6px;
	}
	width: 100%;
	min-width: 150px;
	position: relative;
	display: inline-block;
	.title {
		width: 100%;
		position: relative;
		cursor: pointer;
		height: 35px;
		border-radius: 2px;
		background-color: #ffffff;
		border: 1px #D3D4D6 solid;
		text-align: center;
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		padding: 0 3%;
		transition: .3s;
		.icon {
			font-size: 14px;
			color: #989ca4;
			line-height: 30px;
			margin-left: 10px;
			i {
				font-weight: 400;
				font-size: 16px;
			}
		}
		&:hover{
			border: 1px #4786ff solid;
			
		}
	}
	.searchword {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-size: 15px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #989ca4 !important;
	}
	.iconenter {
		transition: all 0.3s ease;
		transform: rotate(180deg);
	}
	.iconleave {
		transition: all 0.3s ease;
		transform: rotate(0deg);
	}
	.selectlist:hover {
		background-color: #f4f5f7;
	}
	.selectlist>div {
		width: 100%;
	}
	.iconselect {
		color: #5bc2e4;
		font-size: 0.85rem;
		position: absolute;
		left: -0.5px;
	}
}

.hasError {
	.mtyas-single-select {
		.title {
			border: solid 0.5px #f76280;
		}
	}
}
