




















































.v-timer {
    position: relative;
    min-width: 80px;
    .selectPanel {
        background-color: #ffffff;
        display: flex;
        width: 100%;
        height: 200px;
        max-height: 200px;
        border: 1px #dddddd solid;
        position: absolute;
        top: 40px;
        z-index: 10000;
        padding: 2px;
    }
    .hours,
    .minutes {
        width: 50%;
        height: 100%;
        overflow: auto;
        div {
            height: 35px;
            width: 100%;
            text-align: center;
            line-height: 35px;
            cursor: pointer;
            &:hover {
                background-color: #f2f2f2;
            }
        }
        .active {
            background-color: #0380d3;
            color: #fff;
            &:hover {
                background-color: #0380d3;
            }
        }
    }
}
