



























 
	.lay-filter{
		position: relative;
		padding: 10px 20px 0px 20px;
		min-height: 50px;
		box-shadow: 0px 2px 10px #f7f7f7;
		z-index: 10;
		// .filter-one{
		// 	min-height: 50px;
		// }
		.filter-one,.filter-two,.filter-three{
			position: relative;
			display: flex;
			flex-flow: wrap;
			align-items: center;
			div{
                margin-right: 10px;
                margin-bottom: 10px;
			}
			
		}
		.label{
			font-size: 16px;
			margin-right: 10px;
			padding: 0px 5px;
			background-color: #4786FF;
			color: #ffffff;
			border-radius: 2px;
		}
	}
