



















































	.btn-group {
        position: relative;
		display: inline-block;
		margin-right: 10px;
	}
	
	.btn {
		display: inline-block;
		line-height: 1;
		white-space: nowrap;
		cursor: pointer;
		background: #fff;
		border: 1px solid #dcdfe6;
		border-color: #dcdfe6;
		color: #606266;
		-webkit-appearance: none;
		text-align: center;
		box-sizing: border-box;
		outline: none;
		margin: 0;
		transition: all .3s ease;
		font-weight: 500;
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		padding: 8px 15px;
		font-size: 14px;
		border-radius: 2px;
		box-shadow: 1px 2px 3px #f4f4f4
    }
	.sm{
        padding: 5px 6px !important;
        font-size: 12px;
    }
	.btn:hover,
	.btn:focus,
	.btn.focus {
		opacity: 0.7;
		text-decoration: none;
	}
	
	.btn:active,
	.btn.active {
		box-shadow: none;
		transform: translate3d(0, 2px, 0);
	}
	
	.btn.disabled,
	.btn[disabled] {
		cursor: not-allowed;
		filter: alpha(opacity=65);
		-webkit-box-shadow: none;
		box-shadow: none;
		color: #909399;
		background: #f4f4f5;
		border-color: #d3d4d6;
	}
	
	a.btn.disabled,
	.btn-default {
		color: #333333;
		background-color: #ffffff;
		border-color: #cccccc;
		&:hover {
			color: #333333;
			background-color: #e6e6e6;
			border-color: #adadad;
			background-color: transparent;
		}
		.badge {
			color: #ffffff;
			background-color: #333333;
		}
	}
	
	.primary {
		color: #409EFF;
		background: #ecf5ff;
		border-color: #b3d8ff;
	}
	
	.success {
		color: #67c23a;
		background: #f0f9eb;
		border-color: #c2e7b0;
	}
	
	.info {
		color: #909399;
		background: #f4f4f5;
		border-color: #d3d4d6;
	}
	
	.warning {
		color: #e6a23c;
		background: #fdf6ec;
		border-color: #f5dab1;
	}
	
	.danger {
		color: #f56c6c;
		background: #fef0f0;
		border-color: #fbc4c4;
	}
