.radioSelect,
.radioSelectOn {
    display: inline-block;
    position: relative;
    height: 25px;
    width: 45px;
    background: #f4f5f7;
    border-radius: 30px;
    cursor: pointer;
}

.radioSelectOn {
    background: #a7d265;
}

.radioSelect:before,
.radioSelectOn:before {
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    content: '';
    height: 21px;
    width: 21px;
    background: #fff;
    top: 2px;
}

.radioSelect:before {
    right: 21px;
}

.radioSelectOn:before {
    right: 3px;
}

//表单校验公用样式--start
.textarex-tips {
    span.tips {
        top: auto !important;
        bottom: -22px !important;
    }
}

.invalidate-input-box {
    position: relative;
    display: inline-block;

    input.form-control {
        outline: none;
        min-width: 280px;
        display: inline-block;
        margin-right: -3px;
        font-size: 14px;
        color: #2a2a2a;
        letter-spacing: 0;
        vertical-align: initial;
        height: 35px;
        line-height: 35px;
        border-radius: 2px;
        background-color: #f4f5f7;
        border: solid 0.5px #f4f5f7;
        margin-left: 0px;
        text-indent: 10px;
        box-shadow: none;
        box-sizing: border-box;
        -moz-box-sizing: border-box;
    }

    /* 弹出框中textarea默认样式 */
    .textarea-control {
        outline: none;
        width: 304px;
        height: 130px;
        text-indent: 10px;
        border-radius: 2px;
        background-color: #f4f5f7;
        padding-top: 10px;
        resize: none;
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        border: solid 0.5px #f4f5f7;
    }

    .textarea-control:focus,
    .textarea-control:hover {
        border: solid 0.5px #4786ff;
        box-shadow: none;
    }

    span.tips {
        display: none;
    }

    &.input-hide {
        border: 0.5px solid #f76280;

        &:hover {
            span.tips {
                display: block !important;
            }
        }
    }

    .input-hide {
        border: 0.5px solid #f76280;

        &:hover {
            span.tips {
                display: block !important;
            }
        }
    }
}

.invalidate-input-box.hasError {
    .form-control {
        border: solid 0.5px #f76280;
        box-shadow: none;
    }

    .form-control:focus,
    .form-control:hover {
        border: solid 0.5px #4786ff;
        box-shadow: none;
    }

    span.tips {
        display: block;
        position: absolute; //top: 28px;
        left: 0px;
        line-height: 17px !important;
        text-align: left !important;
        white-space: nowrap;
        color: #f76280;
        top: 44px;
        font-size: 12px;
        font-weight: 500;
        padding-left: 0 !important;

        p {
            height: 17px !important;
            line-height: 17px !important;
        }
    }
}

input.ng-invalid,
textarea.ng-invalid,
input.ng-invalid.form-control,
textarea.ng-invalid.form-control {
    // border: 2px solid #a94442;
    border: 0.5px solid #f76280; // -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    // box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
}

input.ng-invalid:focus,
textarea.ng-invalid:focus,
input.ng-invalid.form-control:focus,
textarea.ng-invalid.form-control:focus {
    border-color: #f76280;
    -webkit-box-shadow: none; // -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483;
    // box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483;
    box-shadow: none;
}

