//监测指挥中心  定义的遮罩层的公用样式
.mask-layer {
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    cursor: pointer;
    padding: 5px 15px;
    z-index: 15;
    filter: alpha(opacity=60);
    -moz-opacity: 0.6;
    -khtml-opacity: 0.6;
    opacity: 0.8;

    p {
        box-sizing: border-box;
        max-height: 100px;
        margin: 10px;
        color: #fff;
        text-align: left;
        letter-spacing: 2px;
        font-size: 14px;
        overflow: hidden;
    }
}

