.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.public_modal {
    min-width: 300px;
    min-height: 200px;
    background-color: white;
    text-align: left;
    border-radius: 3px;
    padding: 0;
    transition: all 0.5s linear;
    //  :first-child {
    //     height: 100%;
    // }
    .header {
        margin: 0 25px;
        height: 70px;
        position: relative;
        border-bottom: 1px dashed #D8D8D8;
        p {
            display: inline-block;
            line-height: 70px;
            font-size: 18px;
        }
        span {
            position: absolute;
            right: 0px;
            font-size: 18px;
            line-height: 64px;
            cursor: pointer;
            &:after {
                color: #C2C5CD;
                content: "X"
            }
        }
        .iconfont {
            position: absolute;
            right: 0px;
            font-size: 18px;
            line-height: 70px;
            cursor: pointer;
        }
    }
    .body {
        overflow: auto;
        margin: 0 25px;
        transition: all 0.5s linear;
        height: calc(100% - 170px);
    }
    .footer {
        position: relative;
        bottom: 0px;
        height: 100px;
        line-height: 100px;
        margin: 0 25px;
        text-align: center;
        button {
            width: 100px;
            height: 40px;
            font-size: 16px;
            cursor: pointer;
            color: white;
            border: none;
            border-radius: 2px;
            &:first-child {
                margin-right: 40px;
            }
            &.confirm {
                background: #409499;
                &:hover {
                    background: #26674c;
                }
            }
            &.cancel {
                background: #C2C5CD;
                &:hover {
                    background: #999;
                }
            }
            &:focus {
                outline: none;
            }
        }
    }
}